@import 'imports/vars.scss';

.group-articles,
.tests,
.wrapper-contents,
.commits,
.group-linksarticl-small,
.index,
.row-top,
.row-bottom { display: flex;}

.group-articles { padding-top: 26px;}

.large-column {
   
    width: 520px;
    flex-shrink: 0;
    border: solid 1px rgba(59, 92, 123, 0.1);
    position: relative;

    &:after {

		content: "";
		position: absolute;
		left: 20px;
		top: 273px;
		border: 9px solid transparent;
		border-bottom: 9px solid rgb(255, 255, 255);
		transition: 0.5s;
    }

    .link-page {

	    column-width: 345px;
	    max-height: 75px;
	    height: 100%;
	    overflow: hidden;
		font: 18px/1.22 $font-serif;
		color: $color2;
		padding: 14px 20px;

		 &:hover {color: $color; }
	}

	 img {
	    width: 100%;
	    height: 290px;
	    display: block;
	}
}

.small-column { 

	margin-left: 20px;
	min-width: 500px;
}

.link-articl {
    
    flex: 1 1 198px;
  	font: 16px/1.38 $font-serif;
  	color: rgba(59, 92, 123, 0.8);
  	border-bottom: 1px solid rgba(72, 92, 121, 0.1);
  	column-width: 210px;
	max-height: 74px;
	overflow: hidden;

	&:hover { color: $color;}
}

.row-top {

    flex-wrap: wrap;

    .link-articl { 

    	margin: 0 0 11px 25px;
	    padding: 0 0 17px 25px;
	    max-height: 64px;
 
    	&:nth-child(3), &:nth-child(4) {border-bottom: 0;}
		
		&:nth-child(3), &:nth-child(1) {margin-left: 0;}
		
		.icon-file-text-o { 

			margin-left: -25px;
            margin-right: 9px;
			font-size: 13px;
		}

    }
}

.row-bottom {

	img {   

		width: 100%;
  		height: 139px;
  		display: block;
  	}

	.wrapper-block {
		
		flex-basis: calc(100%/2 - 8px);
	    border: solid 1px rgba(59, 92, 123, 0.1);
	    margin-right: 20px;
	    position: relative;

	    &:last-of-type { margin-right: 0; }

	    &:after {

			content: "";
			position: absolute;
			left: 20px;
			top: 122px;
			border: 9px solid transparent;
			border-bottom: 9px solid rgb(255, 255, 255);
			transition: 0.5s;
	    }

	     .link-articl { 

	     	border-bottom: 0;
	     	height: 100%;
	     	padding: 11px 14px 16px 19px;

	     	  &:hover { color: $color; }
	    }

	  
	}
}

.tests {

    margin: 60px 0;
    justify-content: space-between;

    .wrapper-block {
		
		flex-basis: calc(100%/4 - 10px);
		align-items: center;
		position: relative;
		border: solid 1px rgba(59, 92, 123, 0.1);

		.label {

			position: absolute;
			display: block;
			font: 14px/1.57 $font-serif;
			font-weight: bold;
			color: $color-white;
			text-transform: uppercase;
			background-color: $color;
			left: 10px;
		    top: 8px;
		    padding: 4px 17px;

		}

		img {

			width: 100%;
			height: 139px;
			display: block;
		}

		.link-page {

		  font: 16px/1.38 $font-serif;
		  color: rgba(59, 92, 123, 0.8);
		  padding: 10px 20px 17px;

		  &:hover {  color: $color; }

		    &:before {

			  	content: "";
				position: absolute;
				left: 20px;
				top: -17px;
				border: 9px solid transparent;
				border-bottom: 9px solid rgb(255, 255, 255);
			}
		}
	}
}

.area-linksarticl { position: relative;

	&.right-content {

		.picture-block { 

			background: url(/images/bolezni.jpeg) no-repeat;
			margin-right: 0;
			order:2;
		}

		.middle { padding: 126px 85px 0 0;  }

		.wrapper-contents { padding: 0 90px 0 0; }

	}

}

.wrapper-contents { padding-left: 90px; }

.picture-block {

    width: 340px;
    height: 400px;
    flex-shrink: 0;
    background: url(/images/simptoms.jpeg) no-repeat;
    background-size: cover;
    margin-right: 60px;

    &.block-center {  

    	background: url(/images/profilactika.jpeg) no-repeat;
    	margin-right: 0;
    	height: 385px;

    }
}

.home-title {

    width: 89.6%;
    display: block;
    font: 24px/1.25 "PT Serif";
    font-weight: bold;
    background-color: rgba(0, 142, 206, 0.9);
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    padding: 25px 30px;
    position: absolute;
    top: 25px;

    &.left { left: 110px; }

    &.right {

		right: 110px;
    	text-align: right;
    	top: 19px;
    }

    &.center {

    	position: relative;
    	width: 100%;
    	text-align: center;
    	top: 11px;
        margin-bottom: 33px;
    }

    &.underlaine {

    position: relative;
    color: $color;
    background-color: transparent;
    padding: 25px 30px 25px 0px;
    margin-bottom: 25px;

     &:after { background: url(/images/blueberries_blue.png) no-repeat center; }

     &:before {

     	content:"";
     	position: absolute;
     	width: 70px;
     	border-bottom: 2px solid $color;
     	bottom: 4px;
     }
}
	    
    &:after {

	content: "";
	display: inline-block;
	width: 24px;
	height: 24px;
	background: url(/images/blueberries_white.png) no-repeat center;
	background-size: cover;
	margin-left: 11px;
			
   }
}

.smallpicture-block.middle {
   
    justify-content: space-between;
    align-items: center;
    padding-top: 125px;
    padding-left: 50px;
    min-width: 570px;

    .wrapper-block {

		flex: 1 1 35%;;
		align-items: flex-start;
		margin: 0;
		padding-bottom: 20px;

		.link-page {

			color: rgba(59, 92, 123, 0.8); 
			margin: 0  20px;
			column-width: 250px;

			&:hover { color: $color; }

		}

	}
}

.populars { margin: 10px 0 56px;
   
    .smallpicture-block.large {

    	.wrapper-block {
    		
		   	flex: 1 1 31.75%;
		   	margin: 23px 10px 0 10px;
		   	padding-bottom: 5px;
		   	align-items: flex-start;

		   	&:nth-child(1), &:nth-child(4), &:nth-child(7) {margin-left: 0;}

		   	.link-page { column-width: 210px; }
		}
    }
}

.commits.short-items { 

	flex-wrap: wrap; 
	margin-top: 6px;
	justify-content: space-between;
	margin-bottom: 50px;

	.row-item {

	    flex: 1 1 28%;
    	margin: 0 10px 0 0;
	    border-bottom: 0;
	    padding: 11px 0px 15px;

	     .content-item { 
	     	column-width: 165px;
            padding-right: 30px;
	    }

	    .wrapp-link { width: 90%; }
	}
}

.group-linksarticl-small { 

	flex-wrap: wrap;
	justify-content: space-between;
	margin-bottom: 10px;

	img { 

	  width: 100%;
	  height: 89px;
	}

	.block-left, .block-right {
       
        width: 31.9%;
        margin-top: 9px;
        justify-content: space-between;
    }

     .wrapper-block {

	    flex-basis: calc(100%/2 - 10px);
	    border: 1px solid #eceef1;
	    position: relative;
	    margin: 8px 0 17px;

	    &:after {

			content: "";
			position: absolute;
			left: 20px;
			top: 71px;
			border: 9px solid transparent;
			border-bottom: 9px solid rgb(255, 255, 255);
			transition: 0.5s;
	    }

	    .link-page {

   			font: 14px/1.29 $font-serif;
			max-height: 58px;
			overflow: hidden;
			padding: 2px 5px 0px 17px;


			&:hover { color: $color; }
		}
	}
}

.index {flex-wrap: wrap;

	a {

		font: 14px/1.29 $font-sans;
		color: rgba(59, 92, 123, 0.8);
		padding: 0px 0 10px;

		&:hover { color: rgb(59, 92, 123);}
	}

	.widget { 

		flex-basis: calc(100%/4 - 23px);
        margin-top: 40px;
        margin-right: 28px;

		.widgettitle  {

			font: 16px/1.13 $font-serif;
			font-weight: bold;
			color: $color2;
			background-color: transparent;
			padding: 2px 0 15px;
			min-height: auto;
		}

	}

	.picture-block {
	    
	    width: 250px;
	    height: 460px;
	    background: url(/images/pop_vopros.jpeg) no-repeat;
	    margin-right: 0;
	}
}