@import 'imports/vars.scss';

.ya-share {

	display: flex;
    flex-wrap: wrap;
    background-color: rgba(182, 219, 255, 0.15);
    padding: 15px 0;
    margin: 40px 0;

    .soc-inf {
	    font: 16px/1.5 $font-serif;
	    font-weight: bold;
	    color: rgba(59, 92, 123, 0.8);
	    padding: 0 17px;
    }

    .ya-share2__list {display: flex;}
   
    .ya-share2__icon {

	    font: 16px "icomoon";
	    color: rgb(255, 255, 255);
	    padding: 4px 7px;
	}

    .ya-share2__link {

	    background-color: $color;
	    position: relative;
	    text-align: center;
	    padding: 3px 0!important;
	    min-width: 60px;
	    margin: 0 5px;

	    &:hover {background-color: rgba(0, 142, 206, 0.75);}
	}

}
.ya-share2__badge {
   
    font: 14px/1.71 $font-serif;
    color: rgb(221, 241, 252);
}

.ya-share2__item_service_vkontakte .ya-share2__icon:before {
    content: "\f189";
}
.ya-share2__item_service_twitter .ya-share2__icon:before {
    content: "\f099";
}
.ya-share2__item_service_odnoklassniki .ya-share2__icon:before {
    content: "\f263 ";
}
.ya-share2__item_service_moimir .ya-share2__icon:before {
    content: ":) ";
    font-weight: bold;
    margin-top: -2px;
}
.ya-share2__item_service_gplus .ya-share2__icon:before {
    content: "\f0d5";
}
.ya-share2__item_service_facebook .ya-share2__icon:before {
    content: "\f09a";
}

.ya-share2__title {display: none; }

.article-area.largepicture-block .wrapper-block .link-page { padding: 14px 20px 20px;}

.wrapper-widget-area {

    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0 0 40px;

    .nobackground {
	   
	    width: 100%;
	    flex-basis: calc(100% - 232px);
	}

    .background {

    	width: 212px;
    	background-color: rgba(248, 250, 255, 0.9);

    	.widgettitle { border-bottom: solid 1px #fff }
		
		.short-items { margin-bottom: 0; }

		.row-item { 

			border-bottom: 1px solid  #fff;
			padding: 15px 6px 17px 17px;
		}

		.content-item { 

			font-size: 14px;
			max-height: 42px;

			&:hover { color: $color;}
		}

		.location.left { 

			font-size: 12px;
			text-overflow: ellipsis;
			height: 15px;
			overflow: hidden;
			width: 174px;
			white-space: nowrap;

		    &:before { 

				content: "\f105";
				display: inline-block;
				font-family: "icomoon";
				font-size: 10px;
				color: rgb(0, 142, 206);
				padding: 0px 4px 0px;
				vertical-align: middle;
			}

		    &:after { content: none;}
		}
	}
		  
}

.link-append,
.answer {
	
	width: 70px;
	height: 30px;
	display: inline-block;
	background-color: $color;
	border-radius: 5px;
	border: 1px solid transparent;
 	font-size: 12px;
   	color: $color-white;
    padding: 4px 0;
    margin: 5px 19px 2px 0;
	text-align: center;
	font-weight: normal;
	float: right;
}

.link-append {

	background-color: transparent;
	border: 1px solid $color-white;
	margin: 0 0 0 auto;

	&:hover { background-color: rgba(15, 67, 90, 0.24);}
}


.title-commits {

	display: inline-flex;
	width: 100%;
	background-color: $color;
	font-family: $font-serif;
	font-weight: 700;
	color: $color-white;
	padding: 10px 20px;
	margin-bottom: 19px;

	.count { 

		padding: 0 10px;
		opacity: 0.8;
	}
}

.submit:hover,
.answer:hover  { 
		
		background-color: transparent;
		border: 1px solid $color;
		color: $color; 
	}

.ul-commits { padding: 0;

	>li:last-of-type .text-commit { border-bottom: 0;}
}

.ul-commits  li { display: block;

	&:before { content: none;}

	  ul { padding-left: 65px;

	  	@media screen and (max-width: 1023px) { padding-left: 10px; }

	    li:before {

			content: "\f105";        
        	display: inline-block;
	        font: 14px $icomoon;
	        color: $color;
	        padding: 15px 0 0 0;
	        margin-left: 0;
	        margin-right: 20px;
	        vertical-align: top;
	        font-weight: bold;
    	}

        ul { padding-left: 80px;

        	@media screen and (max-width: 1023px) { padding-left: 20px; }

	       > li:before {
	        	
	        	content: "\f105 \f105";    
		        display: inline-block;
		        font: 14px $icomoon;
	       		color: $color;
		        vertical-align: top;
		        letter-spacing: 4px;
		        font-weight: bold;
	    	}
	    }
    }   
}

.commit-item span { display: inline-block; }

.icon {

	width: 32px;
 	height: 32px;
  	background-image: url(/images/user.png);
  	background-repeat: no-repeat;
  	background-size: contain;
  	margin-right: 15px;
}

.time {

  color: $color;
  font-size: 14px;
}

.text-commit { 

	border-bottom: 1px solid #f9faff;
	font-size: 14px;
	line-height: 1.71;
    padding: 10px 10px 10px 0;
    margin-bottom: 9px;	

}

input[type="submit"] {

	background-color: $color; 
	border: 1px solid transparent;
	margin: 15px 0 15px auto;
	padding: 4px 0;
}
 
.comment-form { 

	background-color: #f9faff;
	padding: 20px 40px;

	* {
		border-radius: 5px;
		border: 0;
		background-color: $color-white;
	}

	.comment-reply-title { 

		display: block;
		font: 16px $font-serif;
		background: none;
		color: rgba(59, 92, 123, 0.8);
		font-weight: bold;
		padding: 0 0 15px 0;
	}

	input[type="text"] { 

		width: calc(100%/2 - 13px);
		font: 14px $font-sans;
		padding: 10px;
		margin: 15px 0;

		@media screen and (max-width: 1023px) { flex-basis: 100%; }
	}

	input[type="text"]:first-of-type { margin-right: 20px; }

	input[type="submit"] { 
		
		width: 100%;
		color: $color-white;
		font: 14px $font-sans;
		padding: 10px;
		cursor: pointer;

		&:hover { color: $color; }
	}

	textarea { 

		width: 100%;
		height: 80px;
		padding: 10px;
	}

	input::-webkit-input-placeholder,
	textarea::-webkit-input-placeholder {
		
		color: rgba(59, 92, 123, 0.5);
		font: 14px $font-sans;
	}

	::-moz-placeholder       {

		color: rgba(59, 92, 123, 0.5);
		font: 14px $font-sans;
	}

	:-ms-input-placeholder  {

		color: rgba(59, 92, 123, 0.5);
		font: 14px $font-sans;

	}
}

